<template>
  <div>
    <div class="add">
      <router-link
        v-if="userInfo.money.nowCount < userInfo.money.maxCount"
        tag="a"
        :to="{ path: '/user/capital-add', query: { status: 'add' }}"
      >
        <Icon type="md-add" style="margin-top:-2px;font-size:18px; margin-right:5px;" />资金发布
      </router-link>
      <router-link v-else tag="a" :to="{ path: '/vip'}">资金发布上限,了解会员权益发布更多资金</router-link>
    </div>
    <div class="bg">
      <div class="title">资金列表</div>
      <div style="border-bottom: 1px solid #eaeaea;padding-bottom:20px;">
        <Input
          v-model="seachConfig.name"
          placeholder="资金名称"
          style="width: 200px;margin-right: 10px;"
        />
        <i-Select
          v-model="seachConfig.type"
          style="width:130px;margin-right: 10px"
          placeholder="所有资金类型"
        >
          <i-Option value="-1">所有类型</i-Option>
          <i-Option value="1">个人资金</i-Option>
          <i-Option value="2">企业资金</i-Option>
          <i-Option value="3">政府资金</i-Option>
        </i-Select>
        <i-Select
          v-model="seachConfig.status"
          style="width:130px;margin-right: 10px"
          placeholder="所有资金状态"
        >
          <i-Option value="-1">所有资金状态</i-Option>
          <i-Option value="0">审核中</i-Option>
          <i-Option value="2">审核失败</i-Option>
          <i-Option value="1">审核通过</i-Option>
          <i-Option value="3">系统冻结</i-Option>
        </i-Select>
        <Button icon="ios-search" type="primary" @click="getData">搜 索</Button>
        <br />
      </div>
      <div class="list">
        <br />
        <div v-if="data.length == 0" style="text-align: center;font-size:14px;color:#adadad;">暂时没有资金</div>
        <ul>
          <li v-for="(item, index) in data" :key="index">
            <div class="list_left">
              <div class="list_left_title">
                <span>
                  <img :src="item.logo == '' ? defaultLOGO : imgUrl+item.logo" alt />
                </span>
                <span class="list_left_name">{{item.institutions}}</span>
                <span
                  class="list_left_status"
                  :style="item.status == 0 ? 'color:#e29440'
                  :item.status == 1 ? 'color:#2868d1'
                  :item.status == 2 ? 'color:#f55231' 
                  :item.status == 3 ? 'color:#e29440' 
                  :''"
                >
                  <div
                    class="quan"
                    :style="item.status == 0 ? 'background:#e29440'
                    :item.status == 1 ? 'background:#2868d1'
                    :item.status == 2 ? 'background:#f55231' 
                    :item.status == 3 ? 'background:#e29440' 
                    :''"
                  ></div>
                  {{item.status == 0 ? '审核中'
                  :item.status == 1 ? '审核通过'
                  :item.status == 2 ? '审核失败'
                  :item.status == 3 ? '系统冻结'
                  :'未知状态' }}
                </span>
              </div>
              <div class="cl"></div>
              <div class="list_left_content">
                <ul>
                  <li>
                    资金类型：
                    <Tag
                      color="warning"
                    >{{item.type == 1 ? '个 人' :item.type == 2 ? '企 业' :item.type == 1 ? '政 府' : '未知类型'}}</Tag>
                  </li>
                  <li>{{item.auditingName}}</li>
                  <li>偏好投资轮次：{{item.loveRound}}</li>
                  <li>联系人：{{item.contactName}}</li>
                  <li>联系方式：{{item.contactPhone}}</li>
                  <li>联系邮箱：{{item.contactEmail}}</li>
                  <li>投资领域：{{item.territory}}</li>
                  <li>创建时间：{{item.createTime.split(' ')[0]}}</li>
                </ul>
                <div class="cl"></div>
                <div class="btns">
                  <Poptip
                    v-if="item.status== 1 || item.status == 2"
                    confirm
                    title="修改资金，资金将会重新审核请确认"
                    @on-ok="capitalEdit(item.id)"
                  >
                    <Button style>修改资金</Button>
                  </Poptip>
                  <Button
                    v-if="item.status == 1"
                    :to="`/user/capital-timeline-index/${item.investments}`"
                  >追加案例</Button>
                  <Button :to="`/capital/detail/${item.id}`">查看资金</Button>
                </div>
                <div class="cl"></div>
              </div>
            </div>
            <div class="cl"></div>
            <div v-if="item.status == 2 || item.status == 3">
              <Alert
                :type="item.status== 2 ? 'error' : item.status== 3 ? 'warning' : ''"
                show-icon
              >{{item.status == 2 ? `审核失败：${item.fail} ( 修改资金后将重新进入审核 )` : item.status == 3 ? `系统冻结：${item.fail} ( 等待系统解冻或者联系管理员10086 )` :'错误状态'}}</Alert>
            </div>
          </li>
        </ul>
        <div class="cl"></div>
        <div class="page">
          <Page :total="seachConfig.pageTotal" @on-change="pageChange" show-elevator />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
import {
  Icon,
  Button,
  Page,
  Poptip,
  Tag,
  Circle,
  Alert,
  Input,
  Select,
  Option
} from "iview";
import { moneys } from "../../../api/capital";
import { imgUrl } from "../../../utils";
export default {
  components: {
    [Icon.name]: Icon,
    [Page.name]: Page,
    [Page.name]: Page,
    [Poptip.name]: Poptip,
    [Circle.name]: Circle,
    [Tag.name]: Tag,
    [Alert.name]: Alert,
    [Input.name]: Input,
    [Select.name]: Select,
    [Option.name]: Option,
    [Button.name]: Button
  },
  computed: {
    ...mapState("user", ["userInfo"])
  },
  data() {
    return {
      imgUrl: imgUrl,
      defaultLOGO: require("../../../assets/img/user.jpg"),
      //搜索配置
      seachConfig: {
        name: "",
        status: "-1",
        type: "-1",
        pageNo: 1, //当前页
        pageTotal: 1 //总条数
      },
      data: []
    };
  },
  methods: {
    //获取资金数据
    getData() {
      moneys(this.seachConfig).then(res => {
        this.data = res.data.data.rows;
        this.seachConfig.pageTotal = res.data.data.total;
      });
    },
    //分页点击
    pageChange(page) {
      this.seachConfig.pageNo = page;
      this.getData();
    },
    //跳转修改
    capitalEdit(id) {
      this.$router.push({
        path: "/user/capital-add",
        query: { status: "edit", id }
      });
    }
  },
  created() {
    this.getData();
  }
};
</script>
